body {
  margin: 0;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
/* Super Classes */
.text_overflow{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.text {
  font-size: 12px;
  color: grey;
}
.flex-center{
    display: flex;
    justify-content: center;
    align-items: center;
}

/* /Super Classes */

.header {
  padding: 24px;
  border: 1px solid;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo img {
  width: 105px;
  height: 52px;
  border-radius: 50%;
}
.nav-items {
  padding: 0;
  display: flex;
  list-style: none;
}
.nav-item {
  padding: 0 14px;
}
.product_card {
    width: 210px;
    text-align: left;
    position: relative;
    vertical-align: top;
    overflow: hidden;
    display: inline-block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0 10px 30px;
    padding: 14px;
    border-radius: 14px;
    width: 210px;
    overflow: hidden;
}
.product-productMetaInfo {
    position: relative;
    z-index: 3;
    background: #fff;
    padding: 0 10px;
    height: 100%;
    margin-top: 12px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    overflow: hidden;
}
.product_card .product_card {
  margin-bottom: 14px;
}
.product-base {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-align: stretch;
    -ms-flex-align: stretch;
    align-items: stretch;
    -ms-flex-line-pack: stretch;
    align-content: stretch;
    margin: 0 -10px 0 3px;
    width: 100%;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.product_img{
    height: 280px;
    width: 100%;
}
.product_brand {
    font-size: 16px;
    font-weight: 700;
    line-height: 1;
    color: #282c3f;
    margin-bottom: 6px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.product_product{
    color: #535766;
    font-size: 14px;
    line-height: 1;
    margin-bottom: 0;
    margin-top: 0;
    font-weight: 400;
    display: block;
}
.product-mexpress, .product-price {
    font-size: 14px;
    line-height: 15px;
    color: #282c3f;
    white-space: nowrap;
}
.product_sell_price{
    font-size: 14px;
    font-weight: 700;
    color: #282c3f;
}
.product_mrp{
    text-decoration: line-through;
    color: #7e818c;
    font-weight: 400;
    margin-left: 5px;
    font-size: 12px;
}
.product-discountPercentage {
    color: #ff905a;
    font-weight: 400;
    font-size: 12px;
    margin-left: 5px;
}

